import React, { useState, useEffect } from 'react';
import { useScenario } from '../../context/ScenarioContext';
import { loadScenariosName } from '../../services/api/scenarioServices';
import Autocomplete from 'react-autocomplete';
import "./topBar.css";

const TopBar = () => {
  const [scenarioName, setScenarioName] = useState('');
  const [scenariosList, setScenariosList] = useState([]);
  const { saveScenario, loadScenario, updateScenario } = useScenario();

  // Carica i nomi degli scenari quando il componente viene montato
  useEffect(() => {
    const fetchScenarios = async () => {
      const scenarios = await loadScenariosName();
      if (!scenarios.error) {
        setScenariosList(scenarios); // Imposta la lista dei nomi degli scenari
      }
    };
    fetchScenarios();
  }, []);

  const handleSaveScenario = () => {
    if (scenarioName) {
      saveScenario(scenarioName);
    } else {
      alert('Inserisci un nome per lo scenario.');
    }
  };

  const handleLoadScenario = () => {
    if (scenarioName) {
      loadScenario(scenarioName);
    } else {
      alert('Inserisci un nome per lo scenario da caricare.');
    }
  };

  const handleUpdateScenario = () => {
    updateScenario();
  };

  return (
    <div className="topbar">
      <Autocomplete
        getItemValue={(item) => item}
        items={scenariosList}
        renderItem={(item, isHighlighted) => (
          <div
            key={item}
            className={isHighlighted ? "react-autocomplete-item-highlighted" : "react-autocomplete-item"}
          >
            {item}
          </div>
        )}
        value={scenarioName}
        onChange={(e) => setScenarioName(e.target.value)}
        onSelect={(val) => setScenarioName(val)}
        inputProps={{ placeholder: "Scenario Name", className: 'react-autocomplete-input' }}
      />
      <button onClick={handleSaveScenario} className="btn-top">Save Scenario</button>
      <button onClick={handleLoadScenario} className="btn-top">Load Scenario</button>
      <button onClick={handleUpdateScenario} className="btn-top">Update Scenario</button>
    </div>
  );
};

export default TopBar;
